import execa from "execa";
import fs from "fs-extra";
import _ from "lodash";
import os from "os";
import path from "path";
import pathKey from "path-key";
import { v4 } from "uuid";

const srcDir = "src";
const binDir = "bin";

const libTestBoilerplate = `
CONFIGURATION TestPlc
  TASK Main(Interval := T#1000ms, Priority := 1);
  PROGRAM P1 WITH Main: TestProgram;
END_CONFIGURATION

PROGRAM TestProgram
  ;
END_PROGRAM
`;

interface Input {
  packageFile: {
    name: string;
    version: string;
    type: string;
    targets?: string[];
    buildArgs?: string[];
  };
  dependencies: Record<
    string,
    {
      name: string;
      path: string;
      dev: boolean;
      apaxYml: {
        type: string;
      } | null;
      files: string[];
    }
  >;
}

function getAdjustedTarget(target: string) {
  // the axunit targets are binary-compatible with their corresponding non-axunit variant
  // this way we avoid libraries having to include both targets
  const targetAdjustment: Record<string, string> = {
    axunit: "1500",
    "axunit-llvm": "llvm",
    plcsim: "1500",
  };
  return targetAdjustment[target] || target;
}

function validateLibraries(input: Input, requiredTargets: string[]) {
  const libraries = Object.values(input.dependencies).filter(
    (dependency) => dependency.apaxYml?.type === "lib"
  );
  for (const library of libraries) {
    const availableTargets = new Set(
      library.files
        .map((file) => file.split("/"))
        .filter((segments) => segments[0] === binDir)
        .map((segments) => getAdjustedTarget(segments[1]))
    );
    // else it would be a src lib
    if (availableTargets.size) {
      for (const requiredTarget of requiredTargets) {
        if (!availableTargets.has(getAdjustedTarget(requiredTarget))) {
          throw new Error(
            `The library ${library.name} is not available for the target ${requiredTarget}. Remove either the library or the target from your apax.yml and try again.`
          );
        }
      }
    }
  }
}

function getBinaryLibraries(
  target: string,
  dev: boolean,
  input: Input
): string[] {
  const adjustedTarget = getAdjustedTarget(target);
  return _.flatten(
    Object.values(input.dependencies)
      .filter(
        (dependency) =>
          dependency.apaxYml?.type === "lib" && dependency.dev === dev
      )
      .map((dependency) =>
        dependency.files
          .filter(
            (file) =>
              file.startsWith(`${binDir}/${adjustedTarget}/`) &&
              file.endsWith(".lib")
          )
          .map((file) => path.join(dependency.path, file))
      )
  );
}

function getSourceLibraries(dev: boolean, input: Input): string[] {
  const dir = `${srcDir}/`;
  return Object.values(input.dependencies)
    .filter(
      (dependency) =>
        dependency.apaxYml?.type === "lib" &&
        dependency.dev === dev &&
        dependency.files.some((file) => file.startsWith(dir))
    )
    .map((dependency) => path.join(dependency.path, dir));
}

function getTargetPlugins(input: Input): string[] {
  const dir = "bin/";
  return Object.values(input.dependencies)
    .filter(
      (dependency) =>
        dependency.apaxYml?.type === "generic" &&
        dependency.name.startsWith("@ax/target-") &&
        dependency.files.some((file) => file.startsWith(dir))
    )
    .map((dependency) => path.join(dependency.path, dir));
}

function getCompileToArgs(
  targetType: string,
  { packageFile }: Input
): string[] {
  return [
    "--compile-to",
    `${targetType}:${getSanitizedName(packageFile.name)}:${
      packageFile.version
    }`,
  ];
}

function getSanitizedName(packageFileName: string) {
  // match npm by converting `@scope/name` -> `scope-name`
  // see https://github.com/npm/cli/blob/66092d5ce22b326c4d4aed1c5073ae7a6a4f4aa2/lib/pack.js#L67-L71

  return `${(packageFileName.startsWith("@")
    ? packageFileName.substr(1)
    : packageFileName
  ).replace(/\//g, "-")}`;
}

export async function build(input: Input) {
  const sourceDirsProd = [
    path.resolve(srcDir),
    ...getSourceLibraries(false, input),
  ];
  const testDir = path.resolve("test");
  const sourceDirsDev = [
    ...((await fs.pathExists(testDir)) ? [testDir] : []),
    ...getSourceLibraries(true, input),
  ];

  const isLib = input.packageFile.type === "lib";
  const libArgs = isLib
    ? getCompileToArgs("lib", input)
    : getCompileToArgs("app", input);
  const targetPaths = getTargetPlugins(input);
  const targetDirs = targetPaths.join(";");
  console.log("target dirs " + targetDirs);

  const { APAX_BUILD_ARGS, APAX_BUILD_TARGETS } = process.env;
  const additionalArgs = APAX_BUILD_ARGS ? JSON.parse(APAX_BUILD_ARGS) : [];

  const pathKeyValue = pathKey();

  const libTestBoilerplateDir = path.join(
    os.tmpdir(),
    `apax-build-lib-test-boilerplate.${v4()}`
  );
  if (isLib) {
    await fs.mkdir(libTestBoilerplateDir);
    await fs.writeFile(
      path.join(libTestBoilerplateDir, "boilerplate.st"),
      libTestBoilerplate
    );
  }

  const targets = APAX_BUILD_TARGETS
    ? (JSON.parse(APAX_BUILD_TARGETS) as string[])
    : input.packageFile.targets || [];
  validateLibraries(input, targets);

  for (const target of targets) {
    const isAxunitTarget = target.startsWith("axunit");

    const sourceDirs = [
      ...sourceDirsProd,
      ...(isAxunitTarget ? sourceDirsDev : []),
      ...(isAxunitTarget && isLib ? [libTestBoilerplateDir] : []),
    ];

    const binaryLibs = [
      ...getBinaryLibraries(target, false, input),
      ...(isAxunitTarget ? getBinaryLibraries(target, true, input) : []),
    ];

    // the `plcsim` target requires some adjustments
    const targetParam = target === "plcsim" ? ["--simulation"] : [];
    const compileTarget = target === "plcsim" ? "1500" : target;

    const stcArgs = _.flattenDeep([
      sourceDirs.map((dir) => ["--input", dir]),
      binaryLibs.map((lib) => ["--lib", lib]),
      targetPaths.length > 1
        ? ["--target", compileTarget, targetParam]
        : [targetParam],
      ["--output", path.join(binDir, target)],
      // when compiling a library for an axunit target, do not actually compile it as a library so that tests can run
      isAxunitTarget ? getCompileToArgs("app", input) : libArgs,
      // debug information is required to calculate coverage information for axunit tests
      isAxunitTarget ? ["--debug"] : [],
      additionalArgs,
    ]);

    console.log(`stc ${stcArgs.join(" ")}`);
    try {
      await execa("stc", stcArgs, {
        stdio: "inherit",
        env: {
          STC_PLUGIN_DIR: targetDirs,
          [pathKeyValue]: [
            // apax changes the "node_modules" folder name to "packages"
            path.join("packages", ".bin"),
            // for compatibility when using npm directly
            path.join("node_modules", ".bin"),
            process.env[pathKeyValue],
          ].join(path.delimiter),
        },
      });
    } catch (error) {
      if ("exitCode" in error) {
        // command was executed and returned with an error
        // just propagate the exit code, no need to show an additional error message
        process.exitCode = error.exitCode;
      } else {
        // command could not be executed for some reason
        throw error;
      }
    }
  }

  // should ideally do this in a `finally` clause
  await fs.remove(libTestBoilerplateDir);
}
