import fs from "fs-extra";
import YAML from "yaml";

import { build } from "./build";

async function run() {
  try {
    await build({
      packageFile: YAML.parse(await fs.readFile("apax.yml", "utf8")),
      dependencies: await fs.readJson(".apax/dependencies.json"),
    });
  } catch (e) {
    console.log(e);
    process.exitCode = 1;
  }
}

void run();
